<template>
    
<div class="default-bttn" :style="cssVars" @click="clicked" type="button">{{ this.title }}</div>
 
</template>

<script>

import { mobileCheck } from "@/utils";

    export default {
        props: ["onclickbttn", "title"],
        data() {
                    return { name: "buttonie button" }
      },
      methods: {
          clicked: function () {  this.onclickbttn() },
      },
      computed: {
            cssVars() {

                return {
                    '--pointer-events': this.$store.state.SelectedUnit.Serial ? "none" : "auto",
                    '--height': mobileCheck() ? "20px" : "30px",
                    '--padding': mobileCheck() ? "8px 10px 5px" : "16px 30px 5px",
                    '--font-size': mobileCheck() ? "8pt" : "11pt",
                }
            }
        },
    }
</script>

<style scoped>
.default-bttn {
    pointer-events: var(--pointer-events);
    height: var(--height);
    font-size: var(--font-size);
    cursor: pointer;
    background: #FFFFFF;
    color: rgba(74, 93, 109, 0.9);
    font-weight: 700;
    padding: var(--padding);
    mix-blend-mode: normal;
    box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.2);
    border-radius: 12px;
    border:none;
}

</style>